/*/////////////////////////////////FormAnimation//////////////////////*/

.box-container2 {
  overflow: hidden;
  width: 100%;
  display:flex;
  flex-direction: row;
}
.box-container2.right-open2 .box1-content2 {
  height: 0;
  flex: 0 1;
  padding: 0;
  opacity: 0;
  transform: translateX(-750px);
  
}
.box-container2.right-open2 .box2-content2 {
  height: 100%;
  top:0;
  flex: 4 1;
  padding: 5%;
  padding-top: 7%;
  padding-bottom: 2%;
  opacity: 1;
}


.box-container2.right-closed2 .box1-content2 {
  overflow:scroll;
  overflow-x:hidden;;
  height: 60vh;
  flex: 4 1;
  opacity: 1;
}

.box-container2.right-closed2 .box1-content2::-webkit-scrollbar-track {border-radius: 4px;}
.box-container2.right-closed2 .box1-content2::-webkit-scrollbar {width: 6px;}
.box-container2.right-closed2 .box1-content2::-webkit-scrollbar-thumb {border-radius: 4px;background: #dfdfdf;}
.box-container2.right-closed2 .box1-content2:hover::-webkit-scrollbar-thumb {background: #000000;}
  
.box-container2.right-closed2 .box2-content2 {
  height: 0;
  transform: translateX(400px);
  flex: 0 1;
  padding: 0;
  opacity: 0;
}
  


.box1-content2, .box2-content2 {
  transition: all 0.5s ease-out;
  color: darkslategray;
  overflow: hidden;
}
.box1-content2 {
  flex: 4 1;
  transition: all 0.5s ease-out;
}
.box2-content2 {
  flex: 0 1;
  transition: all 0.5s ease-out;
}


