.button {
  --text: #fff;
  --cart: #fff;
  --tick: #828282;
  position: relative;
  border: none;
  height: 60px;
  background: none;
  padding: 8px 28px;
  border-radius: 8px;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-mask-image: -webkit-radial-gradient(white, black);
  overflow: hidden;
  cursor: pointer;
  text-align: center;
  min-width: 144px;
  color: var(--text);
  background: var(--background);
  transform: scale(1);
  transform: scale(var(--scale, 1));
  transition: transform 0.4s cubic-bezier(0.36, 1.01, 0.32, 1.27);
}
.button:active {
  --scale: 0.95;
}
.button span {
  font-size: 16px;
  font-weight: 500;
  display: block;
  position: relative;
  padding-left: 24px;
  margin-left: -8px;
  line-height: 26px;
  transform: translateY(0);
  transform: translateY(var(--span-y, 0));
  transition: transform 0.7s ease;
}

.button .cart {
  opacity: 0;
  position: absolute;
  left: 70%;
  top: 50%;
  margin: -13px 0 0 -18px;
  transform-origin: 12px 23px;
  transform: translateX(-160px) rotate(-18deg);
}
.button .cart:before, .button .cart:after {
  content: '';
  position: absolute;
}
.button .cart:before {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  box-shadow: inset 0 0 0 2px var(--cart);
  bottom: 0;
  left: 9px;
  -webkit-filter: drop-shadow(11px 0 0 var(--cart));
          filter: drop-shadow(11px 0 0 var(--cart));
}
.button .cart:after {
  width: 16px;
  height: 9px;
  background: var(--cart);
  left: 9px;
  bottom: 7px;
  transform-origin: 50% 100%;
  transform: perspective(4px) rotateX(-6deg) scaleY(0);
  transform: perspective(4px) rotateX(-6deg) scaleY(var(--fill, 0));
  transition: transform 1.2s ease var(--fill-d);
}
.button .cart svg {
  z-index: 1;
  width: 36px;
  height: 26px;
  display: block;
  position: relative;
  fill: none;
  stroke: var(--cart);
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
}
.button .cart svg polyline:last-child {
  stroke: var(--tick);
  stroke-dasharray: 10px;
  stroke-dashoffset: 10px;
  stroke-dashoffset: var(--offset, 10px);
  transition: stroke-dashoffset 0.4s ease var(--offset-d);
}
.button.loading {
  --scale: 0.95;
  --span-y: -35px;
  --icon-r: 180deg;
  --fill: 1;
  --fill-d: 0.8s;
  --offset: 0;
  --offset-d: 1.73s;
}
.button.loading .cart {
  -webkit-animation: cart 3.4s linear forwards 0.2s;
          animation: cart 3.4s linear forwards 0.2s;
}
@-webkit-keyframes cart {
  12.5% {
    opacity: 0;
    transform: translateX(-60px) rotate(-18deg);
 }
  25%, 45%, 55%, 75% {
    transform: none;
 }
  50% {
    opacity: 1;
    transform: scale(0.9);
 }
  44%, 56% {
    transform-origin: 12px 23px;
 }
  45%, 55% {
    transform-origin: 50% 50%;
 }
  87.5% {
    transform: translateX(70px) rotate(-18deg);
 }
  100% {
    transform: translateX(140px) rotate(-18deg);
 }
}
@keyframes cart {
  12.5% {
    opacity: 0;
    transform: translateX(-60px) rotate(-18deg);
 }
  25%, 45%, 55%, 75% {
    transform: none;
 }
  50% {
    opacity: 1;
    transform: scale(0.9);
 }
  44%, 56% {
    transform-origin: 12px 23px;
 }
  45%, 55% {
    transform-origin: 50% 50%;
 }
  87.5% {
    transform: translateX(70px) rotate(-18deg);
 }
  100% {
    transform: translateX(140px) rotate(-18deg);
 }
}
